'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { 
  FiChevronRight,
  FiHome, 
  FiUser, 
  FiCalendar, 
  FiFileText, 
  FiUsers, 
  FiMessageCircle,
  FiLogOut
} from 'react-icons/fi';
import { useState } from 'react';
import { formatDoctorName } from '@/utils/doctorName';

interface DoctorSidebarProps {
  user: any;
  logout: () => void;
  qualification?: string;
}

const DoctorSidebar = ({ user, logout, qualification }: DoctorSidebarProps) => {
  const pathname = usePathname();
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);

  const getInitials = (name: string) => {
    return name
      ?.split(' ')
      ?.map((n: string) => n[0])
      ?.join('')
      ?.toUpperCase()
      ?.slice(0, 2) || 'D';
  };

  const menu = [
    { href: '/doctor/dashboard', label: 'Dashboard', icon: FiHome },
    { href: '/doctor/appointments', label: 'Appointments', icon: FiCalendar },
    { href: '/doctor/prescriptions', label: 'Prescriptions', icon: FiFileText },
    { href: '/doctor/patients', label: 'Patients', icon: FiUsers },
    { href: '/doctor/chat', label: 'Chats', icon: FiMessageCircle },
    { href: '/doctor/profile', label: 'My Profile', icon: FiUser },
  ];

  return (
    <div className={`${sidebarCollapsed ? 'w-16' : 'w-64'} bg-teal-600 text-white transition-all duration-300 fixed left-0 top-0 h-full z-20`}>
      <div className="p-4">
        {/* Collapse/Expand Button */}
        <button
          onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
          className="w-full flex items-center justify-center p-2 hover:bg-teal-700 rounded-lg transition-colors mb-4"
        >
          <FiChevronRight className={`text-xl transform transition-transform ${sidebarCollapsed ? '' : 'rotate-180'}`} />
        </button>

        {/* Medical Symbol / Logo */}
        {!sidebarCollapsed && (
          <div className="flex items-center justify-center mb-6">
            <div className="flex items-center gap-2">
              <span className="text-2xl">ðŸ©º</span>
              <span className="text-xl font-bold">MediWise</span>
            </div>
          </div>
        )}

        {/* Doctor Profile Section */}
        {!sidebarCollapsed && (
          <div className="mb-6 pb-6 border-b border-teal-500">
            <div className="flex items-center gap-3 mb-3">
              <div className="w-10 h-10 rounded-full bg-white bg-opacity-20 flex items-center justify-center text-sm font-semibold">
                {getInitials(user?.name || 'D')}
              </div>
              <div className="flex-1 min-w-0">
                <p className="text-sm font-semibold truncate">Dr. {user?.name}</p>
                <p className="text-xs text-teal-200 capitalize">Doctor</p>
              </div>
            </div>
          </div>
        )}

        {/* Navigation Links */}
        <nav className="space-y-1">
          {menu.map((item) => {
            const Icon = item.icon;
            const isActive = pathname === item.href;
            return (
              <Link
                key={item.href}
                href={item.href}
                className={`flex items-center gap-3 px-4 py-3 rounded-lg transition-colors ${
                  isActive
                    ? 'bg-teal-700 text-white font-semibold'
                    : 'text-teal-100 hover:bg-teal-700 hover:text-white'
                }`}
              >
                <Icon className="text-xl shrink-0" />
                {!sidebarCollapsed && <span className="text-sm">{item.label}</span>}
              </Link>
            );
          })}
        </nav>

        {/* Logout Button */}
        {!sidebarCollapsed && (
          <div className="absolute bottom-4 left-4 right-4">
            <button 
              onClick={logout}
              className="w-full flex items-center gap-3 px-4 py-3 rounded-lg text-teal-100 hover:bg-teal-700 transition-colors"
            >
              <FiLogOut className="text-xl" />
              <span className="text-sm">LOGOUT</span>
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default DoctorSidebar;

